<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="matoSendingDialog"
        >
            <template v-slot:activator="{ on, attrs }">
                    <v-btn class="primaryBtn mr-2" v-bind="attrs"
                        v-on="on" elevation="0" style="width: auto;">
                        <img src="@/assets/images/export.svg"
                         alt="export" class="mr-1" width="22">
                        <span class="fs-16px">Jo‘natish</span>
                    </v-btn>
            </template>
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title" id="ffas">
                        <span>Bo`yoqxonaga mato jo`natilmoqda</span>
                        <v-btn icon @click="closeDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formMatoSending" v-slot="{ handleSubmit, invalid }">
                        <form @submit.prevent="handleSubmit(onSubmit3)">

                            <div class="mt-4">
                                <span class="text-black ml-1">Sana</span>
                                
                                    <v-menu
                                        v-model="dateMenu"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="auto"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field
                                                    class="mt-2"
                                                    placeholder="Tanlang"
                                                    v-model="dateFormatted"
                                                    readonly
                                                    outlined
                                                    v-bind="attrs"
                                                    v-on="on"
                                                    @blur="date = parseDate(dateFormatted)"
                                                    :error-messages="errors"
                                                ></v-text-field>
                                            </ValidationProvider>
                                        </template>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-date-picker
                                            color="blue lighten-1"
                                            v-model="date"
                                            @input="dateMenu = false"
                                            locale="ru-ru"
                                            first-day-of-week="1"
                                            :error-messages="errors"
                                        ></v-date-picker>
                                        </ValidationProvider>
                                    </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Partiya raqami</span>
                                <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'code']}" 
                                    v-slot="{ errors }">

                                    <v-text-field outlined class="mt-2" :error-messages="errors"
                                        placeholder="Misol: 1234" v-model="party_code">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div>
                                <span class="text-black ml-1">Boyoqxonani tanlang</span>
                                <ValidationProvider :rules="{required: true}"
                                    v-slot="{ errors }">
                                    <v-select :items="paintingFactoriesList" item-text="name" 
                                        :error-messages="errors" item-value="id" v-model="factorySelected"
                                        label="Tanlang" outlined class="mt-2" >
                                    </v-select>
                                </ValidationProvider>
                            </div>



                            <div>
                                <span class="text-black ml-1">Matoni turini tanlang</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="matoTuriSelectDialog = true"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingMatoTuri.name"
                                        :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>



                            <div>
                                <span class="text-black ml-1">Mahsulotni tanlang</span>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-text-field append-icon="mdi-chevron-right"
                                        @click="matoSelectDialog = true"
                                        label="Tanlang" outlined class="mt-2"
                                        :value="existingMato.name"
                                        :error-messages="errors" :disabled="!existingMatoTuri">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div class="aksessuar_turi_add__content">

                                <div>
                                    <span class="text-black ml-1">Rang buyurtma</span>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-text-field outlined class="mt-2" :error-messages="errors"
                                            placeholder="Misol: 1234" v-model="color_code">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>



                                <div>
                                    <span class="text-black ml-1">Massa (kg)</span>
                                    <ValidationProvider :rules="{required: true, 
                                        error: [errorStatus, errorMessage, errorPlace, 'amount']}" 
                                        v-slot="{ errors }">

                                        <v-text-field outlined class="mt-2" :error-messages="errors"
                                            @input="formatPrice(mato.amount, 'amount')"
                                            placeholder="KG kiriting" v-model="mato.amount">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>



                                <div>
                                    <span class="text-black ml-1">KG summa</span>
                                    <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'price']}"
                                     v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(mato.price, 'price')"
                                            v-model="mato.price" min="1"
                                            outlined placeholder="Misol: 240,000 so’m" class="mt-2">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Umumiy summa</span>
                                    <ValidationProvider :rules="{required: true, 
                                    error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                                     v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(mato.total_price, 'total_price')"
                                            :value="getTotalPrice" outlined min="1"
                                            placeholder="Misol: 1,240,000 so’m"
                                            class="mt-2" v-model="mato.total_price">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>


                                <div>
                                    <span class="text-black ml-1">Berilgan summa</span>
                                    <ValidationProvider
                                     :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}"
                                      v-slot="{ errors }">
                                        <v-text-field :error-messages="errors"
                                            @input="formatPrice(mato.paid, 'paid')"
                                            outlined min="1"
                                            placeholder="Misol: 1,000,000 so’m"
                                            class="mt-2" v-model="mato.paid">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>

                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    @click="loader = 'loading'"
                                    type="submit"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="matoTuriSelectDialog"
            style="z-index: 999;"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title" id="ffas">
                        <span>Mato turini tanlang</span>
                        <v-btn icon @click="matoTuriSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductTypeSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit1)">

                            <!-- <div class="exists mt-6">
                                <span class="exists__title text-black">Mavjud matoni tanlang</span>
                                <v-text-field outlined class="exists__input" v-model="inputSearch">
                                    <template v-slot:prepend-inner >
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div> -->

                            <div class="mt-6"></div>
                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in existingMatoTurlari" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-radio-group class="radioGroup" v-model="existingMatoTuri" 
                                        :error-messages="errors">
                                        <v-radio :value="element"></v-radio>
                                    </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="matoTuriSelectDialog = false"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="matoSelectDialog"
            style="z-index: 999;"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title" id="ffas">
                        <span>Mahsulotni tanlang</span>
                        <v-btn icon @click="matoSelectDialog = false">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit2)">

                            <div class="exists mt-6">
                                <span class="exists__title text-black">Mavjud matoni tanlang</span>
                                <v-text-field outlined class="exists__input" v-model="inputSearch">
                                    <template v-slot:prepend-inner >
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>


                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index2) in existingMatos" :key="index2">
                                    <span>{{element.product.name}} - #{{element.product.code}} - {{formatPrice(element.amount)}} kg</span>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-radio-group class="radioGroup" v-model="existingMato" 
                                        :error-messages="errors">
                                        <v-radio :value="element.product"></v-radio>
                                    </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="matoSelectDialog = false"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>

    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
// import colorpicker from '../../VueColor.vue'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
export default {
    props: [''],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar,
        // colorpicker
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            color_code: '',
            party_code: '',
            matoSendingDialog: false,
            matoTuriSelectDialog: false,
            matoSelectDialog: false,
            inputSearch:'',
            existingMatoTurlari: [],
            existingMatos: [],
            mato:{
                amount:'',
                price: 0,
                total_price: '',
                paid: 0
            },
            existingMatoTuri: '',
            existingMato: '',

            factorySelected: '',
            paintingFactoriesList: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= ERRORS START =============
        party_code(newValue, oldValue){
            if(newValue){
                this.clearErrors()
                
            }
        },

        color_code(newValue, oldValue){
            if(newValue){
                this.clearErrors()
                
            }
        },

        'mato.amount': {
            handler(newValue){
                if(newValue){
                    this.clearErrors()
                    let element = this.existingMatos.filter((e) => e.product.id == this.existingMato.id);
                    if(newValue.toString().replace(/,/g, '') > element[0].amount){
                        this.errorPlace = 'amount'
                        this.errorStatus = true
                        this.errorMessage = 'Kiritilgan massa miqdori mavjud miqdordan oshib ketdi!'
                        this.$toast.error(this.errorMessage)
                    }
                }   
            }, deep: true,
        },
        'mato': {
            handler(newValue){
                if(newValue){
                    this.clearErrors()
                }   
            }, deep: true,
        },
        // ============= ERRORS END =============






        // ============= OTHER_WATCHERS START =============
        async inputSearch(newValue, oldValue){
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                if(newValue){
                    await this.fetchMavjudProducts({page: this.page, parent_id: this.existingMatoTuri.id, storage_type: 1, search: newValue})
                }else if(newValue == ''){
                    await this.fetchMavjudProducts({page: this.page, parent_id: this.existingMatoTuri.id, storage_type: 1})
                }
                this.existingMatos = this.mavjud_products.data
            }, 500)
        },
        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                mavjud_products: state => state.warehouse.mavjud_products,
                productCategories: state => state.categories.productCategories,
                factories: state => state.categories.factories
            }
        ),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.mato.price.toString().replace(/,/g, '')
            let amount = this.mato.amount.toString().replace(/,/g, '');

            let result = price * amount
            
            return  this.mato.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods:{
        ...mapActions(
            {
                fetchMavjudProducts: 'warehouse/fetchMavjudProducts',
                postMatoToPainting: 'warehouse/postMatoToPainting',
                fetchCategories: 'categories/fetchCategories',
                fetchFactories: 'categories/fetchFactories',
            }
        ),

        



        // ============= REFRESH START =============
        async refresh(search){
            await this.fetchFactories();
            this.paintingFactoriesList = this.factories.filter((e) => e.factory_category.type == 'paint')


            await this.fetchCategories();
            this.existingMatoTurlari = this.productCategories.filter(e => e.parent_id == 2)
        },
        // ============= REFRESH START =============





        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.mato.amount = currency
                }else if(input == 'price'){
                    this.mato.price = currency
                }else if(input == 'total_price'){
                    this.mato.total_price = currency
                }else if(input == 'paid'){
                    this.mato.paid = currency
                }
                return currency
            }
        },
        // ============= PRICE_FORMATTER END =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.factorySelected = '';
            this.color_code = '#000000';
            this.party_code = '';
            this.mato = {
                amount:'',
                price: 0,
                total_price: '',
                paid: 0
            },
            this.date = ''
            this.existingMatoTuri = '';
            this.existingMato = '';
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.matoSendingDialog = false
            this.$refs.formMatoSending.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============




        // ============= ON_SUBMIT1 START =============
        async onSubmit1(){
            this.$refs.formProductTypeSelect.validate().then(async(success) => {
                if(success){
                    this.matoTuriSelectDialog = false;
                    
                    await this.fetchMavjudProducts({page: this.page, parent_id: this.existingMatoTuri.id, storage_type: 1})
                    this.existingMatos = this.mavjud_products.data
                }
            })
        },
        // ============= ON_SUBMIT1 END =============



        // ============= ON_SUBMIT2 START =============
        async onSubmit2(){
            this.$refs.formProductSelect.validate().then(async(success) => {
                if(success){
                    this.matoSelectDialog = false;
                }
            })
        },
        // ============= ON_SUBMIT2 END =============




        // ============= ON_SUBMIT3 START =============
        async onSubmit3(){
            this.$refs.formMatoSending.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= SENDING_IP START =============
                    let mato = await this.postMatoToPainting(
                        { 
                            created_at: this.date,
                            code: parseFloat(this.party_code.toString().replace(/,/g, '')),
                            factory_id: this.factorySelected, 
                            product_id: parseInt(this.existingMato.id),
                            amount: parseFloat(this.mato.amount.toString().replace(/,/g, '')),
                            color_code: this.color_code, //#777888
                            price: parseFloat(this.mato.price.toString().replace(/,/g, '')),
                            paid: parseFloat(this.mato.paid.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.mato.total_price.toString().replace(/,/g, '')),
                            type: 'painting'
                        })

                    if(mato.response && mato.response.status == 422){
                        if(mato.response.data.errors.code){
                            this.errorMessage = mato.response.data.errors.code[0];
                            this.errorPlace = 'code'
                        }else if(mato.response.data.errors.amount){
                            this.errorMessage = mato.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(mato.response.data.errors.price){
                            this.errorMessage = mato.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(mato.response.data.errors.paid){
                            this.errorMessage = mato.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }else if(mato.response.data.errors.total_price){
                            this.errorMessage = mato.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }

                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(mato.response && mato.response.status == 409){
                        if(mato.response.data.message){
                            this.errorMessage = mato.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli jo`natildi!'
                        this.$toast.success(toastTitle);
                        this.closeDialog();
                        await this.refresh()
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_EXISTS', true)
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_INPROCESS', true)

                    }
                    // ============= SENDING_IP END =============



                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============

    }

}
</script>


<style>

</style>
