                        </div>
                    </template> -->

                </v-data-table>
            </v-col>
        </v-row>

        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>


    </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js';
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],  
    components:{
        DeleteModal
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Kod', value: 'code'},
                {text: 'Turi', value: 'type'},
                {text: 'Nomi', value: 'title'},
                {text: 'Kimga', value: 'whom'},
                {text: 'Massa (kg)', value: 'mass'},
                // {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
            items2: [],
            // ============= SECONDARY_DATA =============

        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_material_inprocess': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_INPROCESS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                jarayondagi_products: state => state.warehouse.jarayondagi_products,
                searchValue: state => state.main.searchValue
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                fetchJarayondagiProducts: 'warehouse/fetchJarayondagiProducts',
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchJarayondagiProducts({page: this.page, status: 2, search: this.searchValue})

            this.page = this.jarayondagi_products.current_page;
            this.pageCount = Math.ceil(this.jarayondagi_products.total / 20);
            console.log(this.jarayondagi_products)
            if(this.jarayondagi_products.data.length > 10){
                this.items2 = this.jarayondagi_products.data.slice(10, this.jarayondagi_products.data.length);
            }
            this.items = this.jarayondagi_products.data;


            this.items.forEach((element) => {
                element.type = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })

            this.items2.forEach((element) => {
                element.type = element.product.product_category.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })
            this.loading = false
        },

    }
}
</script>

