<script>
import {mapState, mapMutations, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components:{
        DeleteModal
    },
    data(){
        return{
            page: 1,
            pageCount: 0,
            loading: false,
            editModal: false,
            deleteModal: false,
            headers: [
                // {text: 'Status', value: 'status'},
                {text: 'Partiya', value: 'direction'},
                {text: 'Turi', value: 'type'},
                {text: 'Nomi', value: 'title'},
                {text: 'Kodi', value: 'code'},
                {text: 'Massa (kg)', value: 'mass'},
                {text: 'KG narxi', value: 'kg_price'},
                {text: 'Summasi', value: 'sum'},
                {text: 'Sana', value: 'date'},
                // {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],

            items: [],
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_material_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                material_income: state => state.warehouse.material_income,
                searchValue: state => state.main.searchValue
            }
        )
    },
    methods:{
        ...mapActions(
            {
                fetchMaterialIncome: 'warehouse/fetchMaterialIncome'
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchMaterialIncome({page: this.page, status: 2, type: 'material', search: this.searchValue})
            this.items = this.material_income.data

            this.page = this.material_income.current_page;
            this.pageCount = Math.ceil(this.material_income.total / 10);

            this.items.forEach((element) => {
                element.status = true;
                element.direction = element.order.factory.name
                element.type = element.order.product.product_category.name
                element.title = element.order.product.name
                element.code = element.order.product.code
                element.mass = element.amount
                element.kg_price = element.order.price
                element.sum = element.amount * element.order.price
                if(element.created_at)
                element.date = this.$dayjs(element.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

