<template>
    <div>
        <v-row>
            <v-col>
                <v-data-table
                    :headers="headers2"
                    :items="items"
                    item-key="name"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                    class="w-100"
                >
                    <!-- eslint-disable  -->
                    <template v-slot:header.code="{ header }">
                        <span class="table-header">#{{ header.text}}</span>
                    </template>
                    <template v-slot:header.type="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.title="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.mass="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.actions="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>





                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.title="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.title }}</span>
                        </div>
                    </template>
                    <template v-slot:item.mass="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.mass }} kg</span>
                        </div>
                    </template>


                    <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center">
                            <div class="cursor-pointer mr-2 mt-1"
                            @click="(product = item) && (editModal = true )"
                            v-if="$can('update', 'warehouse_mato_mavjud')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1"
                            @click="(element = item) && (deleteModal = true )"
                            v-if="$can('delete', 'warehouse_mato_mavjud')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
            <div style="border-right: 1px solid #ececec"></div>
            <v-col>
                <v-data-table
                    :headers="headers2"
                    :items="items2"
                    item-key="name"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                    class="w-100"
                >
                    <!-- eslint-disable  -->
                    <template v-slot:header.code="{ header }">
                        <span class="table-header">#{{ header.text}}</span>
                    </template>
                    <template v-slot:header.type="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.title="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.mass="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>
                    <template v-slot:header.actions="{ header }">
                        <span class="table-header">{{ header.text}}</span>
                    </template>





                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.title="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.title }}</span>
                        </div>
                    </template>
                    <template v-slot:item.mass="{item}">
                        <div class="d-flex">
                            <span class="text-black">{{ item.mass }} kg</span>
                        </div>
                    </template>


                    <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">      
                            <div class="cursor-pointer mr-2 mt-1"
                            @click="(product = item) && (editModal = true )"
                            v-if="$can('update', 'warehouse_mato_mavjud')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1"
                            @click="(element = item) && (deleteModal = true )"
                            v-if="$can('delete', 'warehouse_mato_mavjud')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>






        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditExistingThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <div class="mt-5">
                            <span class="text-black ml-1">Umumiy massa</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                              v-slot="{ errors }">
                                <v-text-field outlined :value="formatPrice(product.amount)"  class="mt-2"
                                    @input="formatPrice2($event, 'amount')"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>


        <DeleteModal 
        :deleteModal="deleteModal" 
        :user_id="element.id" 
        :actionName="'warehouse/deleteStorage'"
        :modalTitle="'Ip o‘chirilishi'"
        :modalDesc="'Bu ipni o‘chirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components:{
        DeleteModal
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers2: [
                {text: 'Kod', value: 'code'},
                {text: 'Turi', value: 'type'},
                {text: 'Nomi', value: 'title'},
                {text: 'Umumiy massa', value: 'mass'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
            items2: [],
            element: {},


            product: {amount: 0},
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_material_exists': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_EXISTS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    
    computed:{
        ...mapState(
            {
                mavjud_products: state => state.warehouse.mavjud_products,
                searchValue: state => state.main.searchValue
            }
        )
    },
    methods:{
        ...mapActions(
            {
                fetchMavjudProducts: 'warehouse/fetchMavjudProducts',
                updateStorage: 'warehouse/updateStorage',
                deleteStorage: 'warehouse/deleteStorage'
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchMavjudProducts({page: this.page, status: 2, storage_type: 1, search: this.searchValue})
            console.log(this.mavjud_products.data)
            
            this.page = this.mavjud_products.current_page;
            this.pageCount = Math.ceil(this.mavjud_products.total / 20);
            if(this.mavjud_products.data.length > 10){
                this.items2 = this.mavjud_products.data.slice(10, this.mavjud_products.data.length);
            }
            this.items = this.mavjud_products.data;


            this.items.forEach((element) => {
                element.type = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.mass = element.amount
            })

            this.items2.forEach((element) => {
                element.type = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.mass = element.amount
            })
            this.loading = false
        },





        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {amount: 0}
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditExistingThread.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditExistingThread.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let ip = await this.updateStorage(
                        { 
                            id: parseInt(this.product.id),
                            amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        })

                    if(ip.response && ip.response.status == 422){
                        if(ip.response.data.errors.amount){
                            this.errorMessage = ip.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(ip.response && ip.response.status == 409){
                        if(ip.response.data.message){
                            this.errorMessage = ip.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh()
                        this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============
    }
}
</script>

<style>

</style>